body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  margin-left: 20px;
}

.color-map {
  margin: 10px 0;
  width: 200px;
}

.sentence {
  display: block;
  margin-bottom: 10px;
}

.sentence.inactive {
  color: #e4e4e4;
}

.word {
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.word.selected {
  border: 1px solid red;
}

.word span {
  font-size: 25px;
}

.word .pos-chip {
  font-size: 10px;
}

.word.ingr {
  background-color: #f7b6d2;
}

.word.action {
  background-color: #dbdb8d;
}



